export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/admin',
      name: '首页',
      class: 'my-icon-blog-index',
      component: Home
    },
    {
      path: '/admin/imgManage',
      name: '图片管理',
      class: 'my-icon-blog-application-1',
      component: ImgManage
    },
    {
      path: '/admin/faq',
      name: '疑问解答',
      class: 'my-icon-blog-message',
      component: FAQ
    },
    {
      path: '/admin/manager',
      name: '我的工作台',
      redirect: '/admin/manager/my',
      class: 'my-icon-blog-profile',
      hasChild: true,
      children: [
        {
          path: '/admin/manager/my',
          name: '我的信息',
          component: My
        },
        {
          path: '/admin/manager/history',
          name: '发货记录',
          component: History
        },
        {
          path: '/admin/manager/send',
          name: '发货管理',
          component: Send
        }
      ]
    },
    {
      path: '*',
      component: NotFound,
      hidden: true
    }
  ]
})



