import axios from 'axios'

// let baseURL = 'http://119.29.175.180:10002';
let baseURL = 'http://192.168.20.108:10002';

// 注册接口
export const RegUser = data => {
  return axios.post(`${baseURL}/reg`, data)
};

// 登录接口
export const LoginUser = data => {
  return axios.post(`${baseURL}/login`, data, {headers: {'token': '123'}})
};

// 获取首页滑动图片
export const GetCarouselImg = () => {
  return axios.get(`${baseURL}/carousel`)
};



