<template>
  <div class="my-login">
    <login v-if="showLogin" @changeToReg="toReg"></login>
    <reg v-if="!showLogin" @changeToLogin="toLogin"></reg>
  </div>
</template>

<script>
  import Login from './login'
  import Reg from './reg'

  export default {
    name: "login-or-reg",
    data() {
      return {
        showLogin: true
      }
    },
    components: {
      'login': Login,
      'reg': Reg,
      'parent': parent
    },
    methods: {
      toReg() {
        this.showLogin = false
      },
      toLogin() {
        this.showLogin = true
      }
    }
  }
</script>

<style scoped>

</style>



