<template>
  <el-carousel :interval="4000" type="card" height="240px">
    <el-carousel-item v-for="item in imgList" :key="item.id">
      <el-row>
        <el-col>
          <img :src="item.path" alt="" height="240px" width="100%">
        </el-col>
      </el-row>
    </el-carousel-item>
  </el-carousel>
</template>

<script>

  import {GetCarouselImg} from "../api/api";

  export default {
    name: "index",
    data() {
      return {
        imgList: []
      }
    },
    created() {
      this.getCarousel()
    },
    methods: {
      getCarousel() {
        GetCarouselImg().then(res => {
          let arr = res.data.data;
          for (let i = 0; i < arr.length; i++) {
            let newObj = {};
            newObj.id = i;
            newObj.path = arr[i].path;
            this.imgList.push(newObj);
          }
          this.$message({
            message: '加载成功',
            type: 'success'
          });
        }).catch(err => {
          this.$message({
            message: '请求失败' + err.data,
            type: 'error'
          });
        })
      }
    }
  }
</script>

<style scoped>
</style>



