<template>
  <el-row>
    <!--    <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>活动管理</el-breadcrumb-item>
          <el-breadcrumb-item>活动列表</el-breadcrumb-item>
          <el-breadcrumb-item>活动详情</el-breadcrumb-item>
        </el-breadcrumb>-->

    <el-breadcrumb separator=">">
      <el-breadcrumb-item v-for="(item, index) in breadList"
                          :key="index" :to="{path:item.path}">{{ item.name }}
      </el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 右边用户信息以及登陆注册 -->
    <el-col :span="2" class="user">
      <div>
        <el-dropdown>
          <img class="avatar" :src="avatar" alt="">
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="toMy" v-text="username">ChenJiayi</el-dropdown-item>
            <el-dropdown-item @click.native="toSend">我的工作台</el-dropdown-item>
            <el-dropdown-item @click.native="logout" divided>退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-col>
  </el-row>
</template>
<script>
  export default {
    data() {
      return {
        avatar: '',
        username: '',
        breadList: ''
      }
    },
    methods: {
      handleSelect() {
        console.log()
      },
      toHome() {
        this.$router.push('/admin')
      },
      toMy() {
        this.$router.push('/admin/manager/my')
      },
      toSend() {
        this.$router.push('/admin/manager/send')
      },
      logout() {
        sessionStorage.removeItem('user');
        this.$router.push('/admin');
        window.location.reload()
      },
      getBread() {
        this.breadList = this.$route.matched;
        this.$route.matched.forEach((item, index) => {
          item.name === '首页' ? item.path = '/admin' : this.$route.path === item.path;
        });
      }
    },
    created() {
      let user = sessionStorage.getItem('user');
      if (user) {
        user = JSON.parse(user);
        console.log(user.avatar);
        this.avatar = user.avatar;
        this.username = user.username
      }
      this.getBread();
    },
    watch: {
      $route() {
        this.getBread();
      }
    }
  }

</script>
<style scoped>
  .avatar {
    width: 40px;
    height: 40px;
    border-radius: 20px;
    margin-top: 10px;
    cursor: pointer;
  }

  .user {
    height: 60px;
    float: right;
  }
</style>



