<template>
  <el-main style="text-align: center">
    <el-form
      :model="RegForm"
      ref="RegForm"
      :rules="rule"
      label-width="0"
      class="reg-form">
      <h3>用户注册</h3>
      <br>
      <el-form-item prop="username">
        <el-input
          type="text"
          v-model="RegForm.username"
          placeholder="用户名">
        </el-input>
      </el-form-item>

      <el-form-item prop="password">
        <el-input
          type="password"
          v-model="RegForm.password"
          placeholder="密码">
        </el-input>
      </el-form-item>

      <el-form-item prop="confirmPassword">
        <el-input
          type="password"
          v-model="RegForm.confirmPassword"
          placeholder="确认密码">
        </el-input>
      </el-form-item>

      <el-form-item prop="email">
        <el-input
          type="email"
          v-model="RegForm.email"
          placeholder="电子邮箱">
        </el-input>
      </el-form-item>

      <el-form-item prop="phone">
        <el-input
          type="text"
          v-model="RegForm.phone"
          placeholder="电话号码">
        </el-input>
      </el-form-item>

      <el-form-item>
        <el-button
          type="success"
          class="submitBtn"
          round
          @click.native.prevent="submit"
          :loading="reging">
          注册
        </el-button>
        <el-button
          type="primary"
          class="resetBtn"
          round
          @click.native.prevent="reset">
          重置
        </el-button>
        <p>已经有账号,马上去<span class="to" @click="toLogin">登录</span></p>
      </el-form-item>
    </el-form>
  </el-main>
</template>

<script>
  import {RegUser} from "../api/api";

  export default {
    name: "reg",
    data() {
      let confirmPasswordCheck = (rule, value, callback) => {
        if (value === '') {
          return callback(new Error('请输入密码'))
        } else if (value !== this.RegForm.password) {
          return callback(new Error('两次输入密码不一致'))
        } else {
          return callback();
        }
      };

      let phoneCheck = (rule, value, callback) => {
        if (value === '') {
          return callback(new Error('请输入电话号码'))
        } else if (value.toString().length !== 11) {
          return callback(new Error('请输入正确的电话号码'))
        } else {
          return callback()
        }
      };
      return {
        RegForm: {
          username: '',
          password: '',
          confirmPassword: '',
          email: '',
          phone: ''
        },
        reging: false,
        rule: {
          username: [
            {
              required: true,
              max: 14,
              min: 2,
              message: '请输入用户名,长度2-14位',
              trigger: 'blur'
            }
          ],
          password: [
            {
              required: true,
              message: '请输入密码',
              trigger: 'blur'
            }
          ],
          confirmPassword: [
            {
              required: true,
              validator: confirmPasswordCheck,
              trigger: 'blur'
            }
          ],
          email: [
            {
              required: true,
              type: 'email',
              message: '请输入正确的邮箱地址',
              trigger: 'blur'
            }
          ],
          phone: [
            {
              required: true,
              validator: phoneCheck,
              trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      submit() {
        this.$refs.RegForm.validate(valid => {
          if (valid) {
            this.reging = true;
            console.log('请求服务器,正在注册...');
            let RegParams = {
              username: this.RegForm.username,
              password: this.RegForm.password,
              email: this.RegForm.email,
              phone: this.RegForm.phone
            };

            // 调用接口,执行axios请求获取返回数据
            RegUser(RegParams).then(res => {
              this.logining = false;
              this.$message({
                type: 'success',
                message: '注册成功'
              });

              let user = res.data.data;
              sessionStorage.setItem('user', JSON.stringify(user));
              window.location.reload();
            })
          } else {
            console.log('Submit Err')
          }
        })
      },
      reset() {
        this.$refs.RegForm.resetFields()
      },
      toLogin() {
        this.$emit('changeToLogin')
      }

    }
  }
</script>

<style scoped>
  .reg-form {
    margin: 20px auto;
    width: 320px;
    background: #fff;
    box-shadow: 0 0 35px #B4BCCC;
    padding: 30px 30px 0 30px;
    border-radius: 25px;
  }

  .submitBtn {
    width: 65%;
  }

  .to {
    color: #FA5555;
    cursor: pointer;
  }
</style>



