<template>
  <el-col>
    <div class="menu-header">
      <h4>❤ ❤ ❤</h4>
    </div>
    <el-menu
      :default-active="$route.path"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      router
      background-color="#e91e63"
      text-color="#fff"
      active-text-color="#a2f78d">
      <!-- 循环写的路由，其中路由中有  hidden：true 的就不加入循环 -->
      <template
        v-for="route in $router.options.routes"
        v-if="!route.hidden">

        <!-- 循环没有children的路由 -->
        <el-menu-item
          v-if="!route.hasChild"
          :key="route.path"
          :index="route.path">
          <i :class="route.class"></i>
          {{ route.name }}
        </el-menu-item>

        <!-- 循环有children的路由 -->
        <el-submenu v-else :index="route.path">
          <template slot="title">
            <i :class="route.class"></i>
            {{ route.name }}
          </template>
          <el-menu-item
            v-for="child in route.children"
            :index="child.path"
            :key="child.path">
            {{ child.name }}
          </el-menu-item>
        </el-submenu>
      </template>
    </el-menu>
  </el-col>

</template>

<script>
  export default {
    name: "NavBar",
    methods: {

      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }
</script>

<style scoped>
</style>



