import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

// 创建基本状态
const state = {
  isLogin: false,
  LoginUser: {
    username: '',
    avatar: ''
  }
};

// 创建改变状态的方法
const mutations = {
  LOGIN(state) {
    state.isLogin = true;
    let user = sessionStorage.getItem('user');
    state.LoginUser.avatar = user.avatar;
    state.LoginUser.username = user.username
  },
  LOGOUT(state) {
    state.isLogin = false;
    state.LoginUser.username = '';
    state.LoginUser.avatar = ''
  }
};

// 创建驱动actions
const actions = {
  login({commit}) {
    commit('LOGIN')
  },
  logout({commit}) {
    commit('LOGOUT')
  }
};

export default new Vuex.Store({
  state,
  mutations,
  actions
})



