<template>
  <el-table
    :data="tableData"
    width="100%">
    <el-table-column type="index" :index="indexMethod" :span="2"></el-table-column>
    <el-table-column prop="path" label="路径" :span="5"></el-table-column>
    <el-table-column prop="imgPreview" label="图片" :span="8">
      <template slot-scope="scope">
        <img :src="scope.row.imgPreview" alt="" style="width: 240px;height: 96px">
      </template>
    </el-table-column>
    <el-table-column label="操作" :span="5">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">编辑
        </el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除
        </el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
  import {GetCarouselImg} from "../../api/api";

  export default {
    name: "",
    data() {
      return {
        tableData: [],
      }
    },
    created() {
      this.getCarousel()
    },
    methods: {
      indexMethod(index) {
        return index + 1;
      },
      getCarousel() {
        GetCarouselImg().then(res => {
          let arr = res.data.data;
          for (let i = 0; i < arr.length; i++) {
            let newObj = {};
            newObj.path = arr[i].path;
            newObj.imgPreview = arr[i].path;
            this.tableData.push(newObj);
          }
          this.$message({
            message: '加载成功',
            type: 'success'
          });
        }).catch(err => {
          this.$message({
            message: '请求失败' + err.data,
            type: 'error'
          });
        })
      }
    }
  }
</script>

<style scoped>
</style>



