<template>
  <div>
    <h3>Home</h3>
    <h2>Username : {{ username }}</h2>
  </div>
</template>

<script>
  import HeaderL from "../components/header"
  export default {
    data() {
      return {
        breadcrumbs: ["首页"],
        username: ''
      }
    },
    mounted() {
      let user = sessionStorage.getItem('user');
      if (user) {
        user = JSON.parse(user);
        this.username = user.username
      }
    },
    components:{
      HeaderL
    }
  }
</script>

<style scoped>
</style>



