<template>
  <div id="app">
    <div class="login-wrapper" v-if="!showFlag">
      <Index></Index>
      <login-or-reg></login-or-reg>
    </div>
    <el-container v-if="showFlag">
      <el-aside width="240px">
        <nav-bar></nav-bar>
      </el-aside>
      <el-container>
        <el-header>
          <header-ly></header-ly>
        </el-header>
        <el-main>
          <router-view/>
          </transition>
        </el-main>
        <el-footer>
          <footer-ly></footer-ly>
        </el-footer>
      </el-container>
    </el-container>
  </div>
</template>

<script>
  // 导入组件
  import HeaderLy from './components/header'
  import FooterLy from './components/footer'
  import LoginOrReg from './components/LoginOrReg'
  import NavBar from './components/NavBar'
  import Index from './views/index'

  export default {
    name: "app",
    components: {
      HeaderLy,
      FooterLy,
      LoginOrReg,
      NavBar,
      Index
    },
    data() {
      return {
        showFlag: false
      }
    },
    created() {
      let user = sessionStorage.getItem('user');
      if (user) {
        this.showFlag = true
      } else {
        this.$router.push('/admin');
        this.showFlag = false
      }
    }
  }
</script>

<style>
</style>



